@import url(https://fonts.googleapis.com/css2?family=Sedgwick+Ave&family=Sen:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary: #FFC803;
}

body {
  font-family: 'Sen', 'Sedgwick Ave', 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: black;
  color: black;
}

.title {
  font-family: 'Sen';
  font-weight: 800;
  text-transform: uppercase;
  font-size: 3.2vmax;
}

.title.inverted {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  font-size: 4vmax;
  letter-spacing: 0.3em;
}

.title.inverted.black {
  -webkit-text-stroke-color: black;
}

.subtitle {
  font-family: 'Sedgwick Ave';
  text-transform: uppercase;
  font-size: 1.5em;
}

.hide {
  opacity: 0;
}

.button-div {
  padding: 10px 0px;
}

.button {
  font-size: 1.3em;
  padding: 10px 50px;
  min-width: 200px;
  border: none;
  border-radius: 25px;
  font-family: 'Sen';
  font-weight: 800;
  text-transform: uppercase;
  transition: all 0.3s;
}

.button.yellow {
  background-color: #FFC803;
  background-color: var(--primary);
  border: 1px solid #FFC803;
  border: 1px solid var(--primary);
  color: black;
}

.button.yellow:hover {
  background-color: transparent;
  border: 1px solid white;
  color: white;
  text-decoration: underline;
}

.button.black {
  background-color: black;
  border: 1px solid black;
  color: #FFC803;
  color: var(--primary);
}

.button.black:hover {
  background-color: transparent;
  border: 1px solid black;
  color: black;
  text-decoration: underline;
}

@media (max-width: 1024px) {
  .title, .title.inverted {
    font-size: 5vmax;
  }
}
.navigation-wrapper {
    width: 100%;
}

.container {
